:root {
  --cyan: #0ff;
  --cyan-dark: #0cc;
  --cyan-darker: #099;
}
body {
  background-color: black;
  color: var(--cyan);
  font-family: 'Orbitron', sans-serif;
}
@keyframes glow {
  0%, 100% {
    filter: drop-shadow(0 0 2px var(--cyan)) drop-shadow(0 0 4px var(--cyan));
  }
  50% {
    filter: drop-shadow(0 0 4px var(--cyan)) drop-shadow(0 0 8px var(--cyan));
  }
}
@keyframes spin-slow {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
