  0%, 100% {
    opacity: 0.3;
    height: 100%;
  }
  50% {
    opacity: 0.6;
    height: 120%;
  }
}
@keyframes reactor-pulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}
@keyframes text-flicker {
  0%, 100% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  96% {
    opacity: 0.8;
  }
  97% {
    opacity: 1;
  }
  98% {
    opacity: 0.5;
  }
  99% {
    opacity: 1;
  }
}
.animate-glow {
  animation: glow 2s ease-in-out infinite;
}
.animate-spin-slow {
  animation: spin-slow 10s linear infinite;
}
.animate-energy {
  animation: energy-pulse 4s ease-in-out infinite;
}
